.feedback {
  @include padding($top: 130px, $bottom: 130px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $orange-hover-2);

  &__middle {
    @include margin($top: 56px, $bottom: '', $left: '', $right: '');
  }

  &__bottom {
    @include margin($top: 30px, $bottom: '', $left: '', $right: '');
  }
}

.feedbackCard {
  @include padding($all: 30px);
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);
  @include border-radius($all: 20px);

  &__header {
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 37px);
  }

  &__image {
    @include border-radius($all: 2000px);
  }

  &__name {
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(23),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__date {
    @include margin($top: 5px, $bottom: '', $left: '', $right: '');
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(20),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $dark-grey-3);
  }

  &__middle {
    @include margin($top: 36px, $bottom: '', $left: '', $right: '');
  }

  &__review {
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__bottom {
    @include margin($top: 67px, $bottom: '', $left: '', $right: '');
    @include flex($direction: '', $wrap: '', $justify: '', $align: '',
      $content: '', $row-gap: '', $column-gap: ''
    );
  }

  &__info {
    @include padding($top: 10px, $bottom: 10px, $left: 20px, $right: 20px);
    @include border-radius($all: 15px);
    @include border($style: solid, $width: 2px, $color: $light-grey-6);
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
    @include flex-item($grow: 0, $shrink: 1, $basis: '',
      $order: '', $align: ''
    );
  }

  &__strong {
    @include font($family: inherit, $weight: inherit, $size: inherit,
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $orange);
  }
}