* {
  & {
    @include margin($all: 0px);
    @include padding($all: 0px);
    @include display($display: '', $visibility: '', $float: '',
      $clear: '', $writing-mode: '', $block-size: '', $box-sizing: border-box);
  }

  @include focus() {
    @include outline($style: solid, $width: 1px, $color: rgba($color: aqua, $alpha: 60%), $offset: 0);
  }
}

*[hidden] {
  @include display($display: none !important, $visibility: '', $float: '', $clear: '',
    $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
    $opacity: ''
  );
}

@import './tags.scss';