html,
head,
body,
ul,
menu,
ol,
span,
p,
dl,
dt,
dd,
a,
button,
input,
ul,
menu,
ol {
  @include margin($all: 0);
  @include padding($all: 0);
}

ul,
menu,
ol {
  @include list($image: '', $type: none, $position: '');
}

a {
  @include size($width: max-content, $height: '');
  @include font($family: inherit, $weight: inherit, $size: inherit,
    $height: '', $style: '', $stretch: '', $variant: ''
  );
  @include text($align: '', $align-last: '', $indent: '', $spacing: '',
    $hyphens: '', $space: '', $break: '', $color: inherit);
  @include text-decoration($line: none, $style: '', $thickness: '', $color: '');
  @include display($display: block, $visibility: '', $float: '', $clear: '',
    $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
    $opacity: ''
  );
}

button {
  & {
    @include size($width: max-content, $height: '');
    @include settings($accent-color: '', $caret: '', $appearance: '',
      $pointer-events: '', $resize: '', $user-select: '', $cursor: pointer,
      $transform: '', $aspect-ratio: '', $color-scheme: ''
    );
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: transparent);
    @include border($style: none, $width: 0, $color: transparent);
    @include font($family: inherit, $weight: inherit, $size: inherit,
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: left, $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: inherit);
    @include display($display: block, $visibility: '', $float: '', $clear: '',
      $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
      $opacity: ''
    );
  }

  @include focus() {
    @include outline($style: none, $width: '', $color: '', $offset: '');
  }
}