.header {
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $black-2);

  &__top {
    @include padding($top: 12px, $bottom: 10px, $left: '', $right: '');
    @include flex($direction: row, $wrap: wrap, $justify: space-between, $align: center,
      $content: '', $row-gap: '', $column-gap: 10px);
  }

  &__bottom {
    @include padding($top: 16px, $bottom: 16px, $left: 20.5px, $right: '');
  }

  @include max-width($value: map.get($breakpoints, lg1)) {
    &__search {
      @include display($display: none, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__phone {
      @include display($display: none, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__socialMedia {
      @include display($display: none, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__authorization {
      @include display($display: none, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__bottom {
      @include display($display: none, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__nav {
      @include display($display: none, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }
  }

  &__menuBurger {
    @include min-width($value: map.get($breakpoints, lg1)) {
      @include display($display: none, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    @include max-width($value: map.get($breakpoints, lg1)) {
      @include display($display: block, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    @include max-width($value: map.get($breakpoints, xs4)) {
      @include margin($top: -20px, $bottom: '', $left: '', $right: '');
    }
  }
}

.logo {
  @include size($width: max-content, $height: '');
  @include padding($top: '', $bottom: '', $left: '', $right: 4px);
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);
  @include border-radius($all: 10px);
  @include font($family: '', $weight: '', $size: 0,
    $height: '', $style: '', $stretch: '', $variant: ''
  );
}

.search {
  @include min-width($value: map.get($breakpoints, xl1)) {
    @include flex-item($grow: 0, $shrink: 1, $basis: 402px,
      $order: '', $align: ''
    );
  }

  &__form {
    @include size($width: 100%, $height: 42px);
    @include max-size($max-width: 402px, $max-height: '');
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $white-3);
    @include border-radius($all: 50px);
    @include overflow($all: hidden);
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
    @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: ''
    );

    &.active {
      @include outline($style: solid, $width: 1px, $color: $focus, $offset: 0);
    }
  }

  &__input {
    & {
      @include padding($top: 12px, $bottom: 13px, $left: 20px, $right: '');
      @include size($width: 100%, $height: 100%);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: inherit);
      @include border($style: none, $width: '', $color: '');
      @include font($family: inherit, $weight: inherit, $size: inherit,
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: inherit);
      @include flex-item($grow: 1, $shrink: 1, $basis: '',
        $order: '', $align: ''
      );
    }

    @include focus() {
      @include outline($style: none, $width: '', $color: '', $offset: '');
    }

    @include placeholder() {
      @include font($family: inherit, $weight: inherit, $size: inherit,
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: inherit);
    }
  }

  &__submit {
    & {
      @include size($width: calc(20px + 24px + 20px), $height: 100%);
      @include background($image: url('./../assets/images/magnifying-glass.svg'), $position: center,
        $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: inherit);
      @include border($style: none, $width: '', $color: '');
      @include font($family: '', $weight: '', $size: 0,
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include flex-item($grow: 0, $shrink: 0, $basis: '',
        $order: '', $align: ''
      );
    }

    @include focus() {
      @include outline($style: none, $width: '', $color: '', $offset: '');
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $white-2-hover);
    }
  }
}

.phone {
  &__link {
    @include before($content: '', $display: block) {
      @include size($width: 25px, $height: 17px);
      @include background($image: url('./../assets/images/flag.svg'), $position: center,
        $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: ''
      );
    }

    & {
      @include font($family: 'Roboto', $weight: $regular, $size: rem(14),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $white);
      @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
        $content: '', $row-gap: '', $column-gap: 12px);
    }
  }
}

.socialMedia {
  @include font($family: '', $weight: '', $size: 0,
    $height: '', $style: '', $stretch: '', $variant: ''
  );

  &__list {
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 10px);
  }
}

.authorization {
  &__button {
    & {
      @include padding($top: 9.5px, $bottom: 9.5px, $left: 23.5px, $right: 23.5px);
      @include border($style: solid, $width: 1px, $color: $white);
      @include border-radius($all: 10px);
      @include font($family: 'Roboto', $weight: $regular, $size: rem(14),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $white);
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $white-hover);
    }
  }
}

.nav {
  &__list {
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: 10px, $column-gap: 36px);
  }

  &__item {
    @include font($family: 'NunitoSans_10pt', $weight: $regular, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
  }

  &__dropdownList {
    @include min-size($min-width: 200px, $min-height: '');
  }
}