@mixin font-face($name, $weight, $style, $path) {
  @font-face {
    font-family: $name;
    font-weight: $weight;
    font-style: $style;
    src: url($path) format("TrueType");
  }
}

@include font-face('Roboto', $thin, normal, "./../assets/fonts/Roboto/Roboto-Thin.ttf");
@include font-face('Roboto', $thin, italic, "./../assets/fonts/Roboto/Roboto-ThinItalic.ttf");

@include font-face('Roboto', $light, normal, "./../assets/fonts/Roboto/Roboto-Light.ttf");
@include font-face('Roboto', $light, italic, "./../assets/fonts/Roboto/Roboto-LightItalic.ttf");

@include font-face('Roboto', $regular, normal, "./../assets/fonts/Roboto/Roboto-Regular.ttf");

@include font-face('Roboto', $medium, normal, "./../assets/fonts/Roboto/Roboto-Medium.ttf");
@include font-face('Roboto', $medium, italic, "./../assets/fonts/Roboto/Roboto-MediumItalic.ttf");

@include font-face('Roboto', $bold, normal, "./../assets/fonts/Roboto/Roboto-Bold.ttf");
@include font-face('Roboto', $bold, italic, "./../assets/fonts/Roboto/Roboto-BoldItalic.ttf");

@include font-face('Roboto', $black, normal, "./../assets/fonts/Roboto/Roboto-Black.ttf");
@include font-face('Roboto', $black, italic, "./../assets/fonts/Roboto/Roboto-BlackItalic.ttf");