:root {
  --black: #1C1B1F;
  --black-2: #2A292C;
  --full-black: black;
  --full-black-hover: #{rgba($color: black, $alpha: 20%)};
  --full-black-hover-2: #{rgba($color: black, $alpha: 40%)};
  --full-black-hover-3: #{color.scale($color: black, $lightness: 20%)};
  --white: white;
  --white-hover: #{rgba($color: white, $alpha: 15%)};
  --white-2: #EDEDED;
  --white-2-hover: #{rgba($color: #EDEDED, $alpha: 40%)};
  --white-3: #{rgba($color: #EDEDED, $alpha: 20%)};
  --orange: #F79923;
  --orange-hover: #{color.scale($color: #F79923, $blackness: 12%)};
  --orange-hover-2: #{rgba($color: #F79923, $alpha: 23%)};
  --orange-hover-3: #{color.scale($color: #F79923, $blackness: 10%)};
  --background-black: #2A292C;
  --light-grey: #F4F4F4;
  --light-grey-hover: #{color.scale($color: #F4F4F4, $blackness: 20%)};
  --light-grey-2: #F0F0F0;
  --light-grey-3: #BBBBBB;
  --light-grey-4: #E8E8E8;
  --light-grey-5: #CDCDCD;
  --light-grey-6: #D9D9D9;
  --dark-grey: #4F4C4C;
  --dark-grey-2: #3D3C3C;
  --dark-grey-3: #7F7F7F;
  --focus: aqua;
  --green: #46A04F;
  --popup-background: #{rgba($color: black, $alpha: 40%)};
}

$black: var(--black);
$black-2: var(--black-2);
$full-black: var(--full-black);
$full-black-hover: var(--full-black-hover);
$full-black-hover-2: var(--full-black-hover-2);
$white: var(--white);
$white-hover: var(--white-hover);
$white-2: var(--white-2);
$white-2-hover: var(--white-2-hover);
$white-3: var(--white-3);
$orange: var(--orange);
$orange-hover: var(--orange-hover);
$orange-hover-2: var(--orange-hover-2);
$orange-hover-3: var(--orange-hover-3);
$background-black: var(--background-black);
$light-grey: var(--light-grey);
$light-grey-hover: var(--light-grey-hover);
$light-grey-2: var(--light-grey-2);
$light-grey-3: var(--light-grey-3);
$light-grey-4: var(--light-grey-4);
$light-grey-5: var(--light-grey-5);
$light-grey-6: var(--light-grey-6);
$dark-grey: var(--dark-grey);
$dark-grey-2: var(--dark-grey-2);
$dark-grey-3: var(--dark-grey-3);
$focus: var(--focus);
$green: var(--green);
$popup-background: var(--popup-background);