@mixin font-face($name, $weight, $style, $path) {
  @font-face {
    font-family: $name;
    font-weight: $weight;
    font-style: $style;
    src: url($path) format("TrueType");
  }
}

@include font-face('NunitoSans_10pt', $extraLight, normal, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-ExtraLight.ttf");
@include font-face('NunitoSans_10pt', $extraLight, italic, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-ExtraLightItalic.ttf");

@include font-face('NunitoSans_10pt', $light, normal, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-Light.ttf");
@include font-face('NunitoSans_10pt', $light, italic, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-LightItalic.ttf");

@include font-face('NunitoSans_10pt', $regular, normal, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-Regular.ttf");
@include font-face('NunitoSans_10pt', $regular, italic, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-Italic.ttf");

@include font-face('NunitoSans_10pt', $medium, normal, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-Medium.ttf");
@include font-face('NunitoSans_10pt', $medium, italic, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-MediumItalic.ttf");

@include font-face('NunitoSans_10pt', $semiBold, normal, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-SemiBold.ttf");
@include font-face('NunitoSans_10pt', $semiBold, italic, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-SemiBoldItalic.ttf");

@include font-face('NunitoSans_10pt', $bold, normal, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-Bold.ttf");
@include font-face('NunitoSans_10pt', $bold, italic, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-BoldItalic.ttf");

@include font-face('NunitoSans_10pt', $extraBold, normal, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-ExtraBold.ttf");
@include font-face('NunitoSans_10pt', $extraBold, italic, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-ExtraBoldItalic.ttf");

@include font-face('NunitoSans_10pt', $bold, normal, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-Black.ttf");
@include font-face('NunitoSans_10pt', $bold, italic, "./../assets/fonts/Nunito_Sans/NunitoSans_10pt-BlackItalic.ttf");