.popup {
  @include position($position: absolute, $top: 0, $bottom: '', $left: 0, $right: '',
    $z-index: 3);

  &.active {
    .popup {
      &__background {
        @include background($image: '', $position: '',
          $size: '', $repeat: '', $origin: '', $clip: '',
          $attachment: '', $color: $popup-background );
      }
    }
  }

  &__background {
    @include min-size($min-width: '', $min-height: 100vh);
    @include size($width: 100%, $height: 100%);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: transparent);
    @include transition($duration: 0.5s, $function: ease, $delay: '', $property: '');
    @include flex($direction: row, $wrap: wrap, $justify: center, $align: center,
      $content: '', $row-gap: '', $column-gap: ''
    );
  }

  &__dialog[open] {
    @include padding($all: 20px);
    @include min-size($min-width: 200px, $min-height: 100px);
    @include border($style: none, $width: '', $color: '');
    @include flex($direction: column, $wrap: wrap, $justify: space-between, $align: '', 
      $content: '', $row-gap: '', $column-gap: ''
    );
    @include position($position: sticky, $top: '', $bottom: '', $left: '', $right: '',
      $z-index: ''
    );
  }
}