.leadForm {
  @include padding($top: '', $bottom: 130px + 100px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);

  &__form {
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $light-grey);
    @include border-radius($all: 20px);
    @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: flex-start,
      $content: '', $row-gap: '', $column-gap: 10px);

    @include max-width($value: map.get($breakpoints, lg2)) {
      @include flex($direction: column, $wrap: '', $justify: '', $align: '',
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__left {
    @include padding($all: 40px);
    @include flex-item($grow: '', $shrink: 1, $basis: '',
      $order: '', $align: center);
  }

  &__right {
    @include flex-item($grow: '', $shrink: 1, $basis: '',
      $order: '', $align: flex-end);
  }

  &__image {
    @include margin($top: -50px, $bottom: '', $left: '', $right: '');
    @include size($width: 100%, $height: '');

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include margin($top: 0px, $bottom: '', $left: '', $right: '');
    }
  }

  &__header {
    @include max-size($max-width: 568px, $max-height: '');

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include text($align: center, $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: ''
      );
    }
  }

  &__title {
    @include font($family: 'NunitoSans_10pt', $weight: $bold, $size: rem(29),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__subTitle {
    @include font($family: 'NunitoSans_10pt', $weight: $regular, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__middle {
    @include margin($top: 34px, $bottom: '', $left: '', $right: '');
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: 20px, $column-gap: 10px);
  }

  &__inputField {
    @include flex($direction: column, $wrap: '', $justify: '', $align: '',
      $content: '', $row-gap: 10px, $column-gap: ''
    );
  }

  &__label {
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__tel,
  &__email {
    & {
      @include padding($top: 20px, $bottom: 20px, $left: 30px, $right: 37px);
      @include min-size($min-width: 259px, $min-height: 58px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: transparent);
      @include border-radius($all: 20px);
      @include border($style: solid, $width: 2px, $color: #BBBBBB);
      @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(14),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $black);
    }

    @include invalid() {
      @include outline($style: solid, $width: 1px, $color: red, $offset: 0);
    }

    @include placeholder() {
      @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(14),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $black);
    }
  }

  &__submit {
    & {
      @include padding($top: 16.5px, $bottom: 16.5px, $left: '', $right: '');
      @include size($width: 323px, $height: '');
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: #F79923);
      @include border-radius($all: 20px);
      @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(18),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $white);
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange-hover-3);
    }
  }

  &__bottom {
    @include margin($top: 60px, $bottom: '', $left: '', $right: '');
  }

  &__bottom-top {
    @include flex($direction: row, $wrap: wrap, $justify: space-between, $align: center,
      $content: '', $row-gap: 20px, $column-gap: 35px);

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include flex($direction: '', $wrap: '', $justify: center, $align: '',
        $content: '', $row-gap: '', $column-gap: ''
      );
    }

    @include max-width($value: map.get($breakpoints, md)) {
      @include flex($direction: column, $wrap: '', $justify: '', $align: '',
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__bottom-bottom {
    @include max-width($value: map.get($breakpoints, xl1)) {
      @include flex($direction: '', $wrap: '', $justify: center, $align: '',
        $content: '', $row-gap: '', $column-gap: ''
      );
    }

    @include max-width($value: map.get($breakpoints, lg2)) {
      @include flex($direction: '', $wrap: '', $justify: space-between, $align: '',
        $content: '', $row-gap: '', $column-gap: ''
      );
    }

    @include max-width($value: map.get($breakpoints, md)) {
      @include flex($direction: '', $wrap: '', $justify: center, $align: '',
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__agreement {
    @include margin($top: 15px, $bottom: '', $left: '', $right: '');
  }
}

.socialMediaText {
  &__list {
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 29px);
  }

  &__link {
    & {
      @include font($family: 'Montserrat', $weight: $medium, $size: rem(14),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $full-black);
      @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
        $content: '', $row-gap: '', $column-gap: 6px);
    }

    @include after($content: '', $display: block) {
      @include size($width: 13px, $height: 13px);
      @include background($image: url('./../assets/images/arrow\ \ up\ black.svg'), $position: center,
        $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: ''
      );
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }
}

.agreement {
  @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
    $content: '', $row-gap: '', $column-gap: 20px);

  &__description {
    @include size($width: 284px, $height: '');
    @include font($family: 'Montserrat', $weight: $regular, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $dark-grey-3);
  }
}