.educationAreas {
  @include padding($top: 130px, $bottom: 130px, $left: '', $right: '');
  @include border-radius($top-left: 100px, $top-right: 100px,
    $bottom-left: '', $bottom-right: ''
  );

  &__list {
    @include margin($top: 56px, $bottom: '', $left: '', $right: '');
    @include grid($columns: 608px 1fr, $rows: 1fr 1fr,
      $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
      $row-gap: 10px, $column-gap: 10px, $align-items: '', $justify-items: '',
      $align-content: '', $justify-content: ''
    );

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include grid($columns: 1fr 1fr, $rows: '',
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
        $row-gap: '', $column-gap: '', $align-items: '', $justify-items: '',
        $align-content: '', $justify-content: ''
      );
    }

    @include max-width($value: map.get($breakpoints, md2)) {
      @include grid($columns: 1fr, $rows: '', 
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '', 
        $row-gap: '', $column-gap: '', $align-items: '', $justify-items: '', 
        $align-content: '', $justify-content: ''
      );
    }
  }

  &__item {
    @include padding($all: 20px);
    @include size($width: '', $height: '');
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $light-grey);
    @include border-radius($all: 20px);
    @include flex($direction: column, $wrap: '', $justify: space-between, $align: '',
      $content: '', $row-gap: '', $column-gap: ''
    );
  }

  &__itemHeader {
    @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: flex-start,
      $content: '', $row-gap: 10px, $column-gap: ''
    );
  }

  &__secondTitle {
    @include size($width: '', $height: '');
    @include font($family: 'Montserrat', $weight: $bold, $size: rem(25),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
    @include flex-item($grow: '', $shrink: 1, $basis: 452px,
      $order: '', $align: ''
    );

    @include max-width($value: map.get($breakpoints, lg)) {
      @include flex-item($grow: '', $shrink: '', $basis: 300px,
        $order: '', $align: ''
      );
    }

    @include max-width($value: map.get($breakpoints, md2)) {
      @include flex-item($grow: 1, $shrink: '', $basis: '', 
        $order: '', $align: ''
      );
    }
  }

  &__imageWrapper {
    @include size($width: 70px, $height: 70px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $orange);
    @include border-radius($all: 15px);
    @include flex($direction: '', $wrap: '', $justify: center, $align: center,
      $content: '', $row-gap: '', $column-gap: ''
    );
    @include flex-item($grow: '', $shrink: 0, $basis: '',
      $order: '', $align: ''
    );

    &.primary {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $white);
    }
  }

  &__specialities {
    @include before($content: '', $display: block) {
      @include size($width: 10px, $height: 10px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $green);
      @include border-radius($all: 2000px);
    }

    & {
      @include margin($top: 17px, $bottom: '', $left: '', $right: '');
      @include font($family: 'Montserrat', $weight: $medium, $size: rem(14),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $black);
      @include flex($direction: row, $wrap: nowrap, $justify: '', $align: center,
        $content: '', $row-gap: '', $column-gap: 11px);
    }
  }

  &__salary {
    @include margin($top: 14px, $bottom: '', $left: '', $right: '');
    @include padding($top: 10px, $bottom: 10px, $left: 15px, $right: 15px);
    @include size($width: max-content, $height: '');
    @include border($style: solid, $width: 2px, $color: $light-grey-5);
    @include border-radius($all: 40px);
    @include font($family: 'Montserrat', $weight: $regular, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }
}

.seeAll {
  & {
    @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(20),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $orange);
    @include position($position: relative, $top: '', $bottom: '', $left: '', $right: '',
      $z-index: ''
    );
  }

  @include after($content: '', $display: block) {
    @include margin($top: 11px, $bottom: '', $left: '', $right: '');
    @include size($width: 100%, $height: 2px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $orange);
    @include border-radius($all: 10px);
    @include position($position: absolute, $top: '', $bottom: '', $left: '', $right: '',
      $z-index: ''
    );
  }
}