$containerWidth: calc(1920px - (347px + 347px));

.container {
  @include size($width: $containerWidth, $height: '');
  @include margin($top: '', $bottom: '', $left: auto, $right: auto);

  @include max-width($value: map.get($breakpoints, xl1)) {
    @include size($width: 90%, $height: '');
  }
}