.sliderButtons {
  &__list {
    @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 49px);
  }

  &__item {
    @include font($family: '', $weight: '', $size: rem(0),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
  }

  &__prevButton,
  &__nextButton {
    & {
      @include size($width: 60px, $height: 60px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: #F79923);
      @include border-radius($all: 2000px);
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: color.scale($color: #F79923, $blackness: 20%));
    }
  }

  &__prevButton {
    @include settings($accent-color: '', $caret: '', $appearance: '',
      $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
      $transform: rotate(90deg), $aspect-ratio: '', $color-scheme: ''
    );
  }

  &__nextButton {
    @include settings($accent-color: '', $caret: '', $appearance: '',
      $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
      $transform: rotate(-90deg), $aspect-ratio: '', $color-scheme: ''
    );
  }

  &__prevButtonImage {
    @include size($width: 19.31px, $height: '');
  }

  &__nextButtonImage {
    @include size($width: 19.31px, $height: '');
  }
}