.chooseService {
  @include padding($top: 65px, $bottom: 277px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);

  &__header {
    @include margin($top: '', $bottom: '', $left: auto, $right: auto);
    @include max-size($max-width: 674px, $max-height: '');
  }

  &__subTitle {
    @include margin($top: 24px, $bottom: '', $left: auto, $right: auto);
    @include max-size($max-width: 598px, $max-height: '');
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: center, $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }

  &__middle {
    @include margin($top: 98px, $bottom: '', $left: '', $right: '');
  }

  &__list {
    @include flex($direction: row, $wrap: wrap, $justify: space-evenly, $align: flex-start,
      $content: '', $row-gap: 10px, $column-gap: 10px);
  }
}

.chooseServiceCard {
  @include padding($all: 15px);
  @include size($width: 402px, $height: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $light-grey);
  @include border-radius($all: 20px);

  @include max-width($value: map.get($breakpoints, sm)) {
    @include size($width: 100%, $height: '');
  }

  &__imageWrapper{
    @include position($position: relative, $top: '', $bottom: '', $left: '', $right: '',
      $z-index: ''
    );
    @include border-radius($all: 13px);
    @include overflow($all: hidden);
  }

  &__image{
    @include size($width: 100%, $height: '');
  }

  &__label {
    & {
      @include padding($top: 7.5px, $bottom: 7.5px, $left: 21px, $right: 21px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $black);
      @include border-radius($top-left: 13px, $top-right: '',
        $bottom-left: '', $bottom-right: 13px);
      @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(14),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $white);
      @include flex($direction: row, $wrap: '', $justify: '', $align: center,
        $content: '', $row-gap: '', $column-gap: 11px);
      @include position($position: absolute, $top: 0, $bottom: '', $left: 0, $right: '',
        $z-index: ''
      );
    }

    @include after($content: '', $display: block) {
      @include size($width: 24px, $height: 24px);
      @include background($image: url('./../assets/images/zigzag-arrow.svg'), $position: center,
        $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: ''
      );
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__middle {
    @include margin($top: 14px, $bottom: '', $left: '', $right: '');
  }

  &__title {
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(22),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__subTitle {
    @include margin($top: 7px, $bottom: '', $left: '', $right: '');
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $dark-grey);
  }

  &__bottom {
    @include margin($top: 74px, $bottom: '', $left: '', $right: '');
  }

  &__bottom-top {
    @include flex($direction: row, $wrap: wrap, $justify: '', $align: center,
      $content: '', $row-gap: '', $column-gap: 30px);
  }

  &__prices {
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 15px);
  }

  &__currentPrice {
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(40),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__oldPrice {
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(22),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $dark-grey-3);
  }

  &__time {
    @include padding($top: 6px, $bottom: 6px, $left: 10px, $right: 10px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $green);
    @include border-radius($all: 13px);
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
  }

  &__bottom-bottom {
    @include margin($top: 20px, $bottom: '', $left: '', $right: '');
  }

  &__link {
    & {
      @include padding($top: 20.5px, $bottom: 20.5px, $left: '', $right: '');
      @include size($width: 100%, $height: '');
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange);
      @include border-radius($all: 15px);
      @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(18),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $white);
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: 10px);
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange-hover-3);
    }

    @include after($content: '', $display: block) {
      @include size($width: 16px, $height: 16px);
      @include background($image: url('./../assets/images/arrow\ up2.svg'), $position: center,
        $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: ''
      );
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }
}