.mobile {
  @include min-size($min-width: 100vw, $min-height: 100vh);
  @include size($width: 100%, $height: 100%);
  @include position($position: absolute, $top: 0, $bottom: '', $left: 0, $right: '',
    $z-index: ''
  );

  .header {
    &__search {
      @include display($display: block, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__phone {
      @include display($display: block, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__socialMedia {
      @include display($display: block, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__authorization {
      @include display($display: block, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__bottom {
      @include display($display: block, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }

    &__nav {
      @include display($display: block, $visibility: '', $float: '', $clear: '',
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
        $opacity: ''
      );
    }
  }

  &.active {
    .mobile {
      &__block {
        @include settings($accent-color: '', $caret: '', $appearance: '',
          $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
          $transform: TranslateX(0), $aspect-ratio: '', $color-scheme: ''
        );
      }

      &__background {
        @include background($image: '', $position: '',
          $size: '', $repeat: '', $origin: '', $clip: '',
          $attachment: '', $color: $full-black-hover-2);
      }
    }
  }

  &__background {
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: transparent);
    @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
  }

  &__block {
    @include padding($all: 8px);
    @include size($width: 315px, $height: 100%);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $black);
    @include grid($columns: 190px 1fr, $rows: '',
      $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
      $row-gap: 15px, $column-gap: 10px, $align-items: center, $justify-items: center,
      $align-content: '', $justify-content: ''
    );
    @include settings($accent-color: '', $caret: '', $appearance: '',
      $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
      $transform: TranslateX(-315px), $aspect-ratio: '', $color-scheme: ''
    );
    @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');

    @include max-width($value: map.get($breakpoints, xs3) + 30px) {
      @include size($width: 100%, $height: '');
    }
  }

  .header {
    &__search {
      @include grid-item($row: string.unquote($string: '1/2'), $column: string.unquote($string: '1/3'),
        $area: '', $justify: '', $align: '', $z-index: '', $order: ''
      );
    }

    &__phone {
      @include grid-item($row: string.unquote($string: '2/3'), $column: string.unquote($string: '1/2'),
        $area: '', $justify: '', $align: '', $z-index: '', $order: ''
      );
    }

    &__socialMedia {
      @include grid-item($row: string.unquote($string: '2/3'), $column: string.unquote($string: '2/3'),
        $area: '', $justify: '', $align: '', $z-index: '', $order: ''
      );
    }

    &__authorization {
      @include size($width: 100%, $height: '');
      @include grid-item($row: string.unquote($string: '3/4'), $column: string.unquote($string: '1/3'),
        $area: '', $justify: '', $align: '', $z-index: '', $order: ''
      );
    }

    &__nav {
      @include padding($all: 0);
      @include grid-item($row: string.unquote($string: '4/5'), $column: string.unquote($string: '1/3'),
        $area: '', $justify: left, $align: '', $z-index: '', $order: ''
      );
    }
  }

  .search {
    @include size($width: 100%, $height: '');

    &__form {
      @include max-size($max-width: 100%, $max-height: '');

      @include max-width($value: map.get($breakpoints, xs4)) {
        @include max-size($max-width: 80%, $max-height: '');
      }
    }
  }

  .authorization {
    &__button {
      @include size($width: 100%, $height: '');
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  .nav {
    &__list {
      @include flex($direction: column, $wrap: nowrap, $justify: flex-start, $align: flex-start,
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }
}