.educationInAustria {
  @include margin($top: -100px, $bottom: '', $left: '', $right: '');
  @include padding($top: 130px, $bottom: 130px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);
  @include border-radius($top-left: 100px, $top-right: 100px,
    $bottom-left: '', $bottom-right: ''
  );

  &__middle {
    @include margin($top: 56px, $bottom: '', $left: '', $right: '');
    @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: flex-start,
      $content: '', $row-gap: '', $column-gap: 20px);

    @include max-width($value: map.get($breakpoints, md2)) {
      @include flex($direction: column, $wrap: '', $justify: '', $align: '',
        $content: '', $row-gap: 20px, $column-gap: ''
      );
    }
  }

  &__left {
    // @include size($width: 814px, $height: '');
    @include flex-item($grow: 0, $shrink: 1, $basis: 814px,
      $order: '', $align: ''
    );
    @include flex($direction: column, $wrap: wrap, $justify: flex-start, $align: flex-start,
      $content: '', $row-gap: 10px, $column-gap: ''
    );
  }

  &__right {
    @include max-width($value: map.get($breakpoints, md2)) {
      @include size($width: 80%, $height: '');
      @include flex-item($grow: '', $shrink: '', $basis: '',
        $order: '', $align: center);
    }
  }

  &__list {
    @include flex($direction: column, $wrap: wrap, $justify: flex-start, $align: flex-start,
      $content: '', $row-gap: 26px, $column-gap: ''
    );
  }

  &__item {
    @include size($width: 100%, $height: '');

    @include max-width($value: map.get($breakpoints, md2)) {
      @include flex($direction: '', $wrap: '', $justify: center, $align: '',
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__button {
    @include max-size($max-width: 281px, $max-height: '');
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: break-word, $color: $dark-grey);
    @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');

    @include max-width($value: map.get($breakpoints, md2)) {
      @include max-size($max-width: 100%, $max-height: '');
      @include text($align: center, $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: ''
      );
    }

    &.primary {
      @include font($family: '', $weight: $bold, $size: rem(20),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $full-black);
    }
  }
}