.intro {
  @include padding($top: 52px, $bottom: 61px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $black-2);
  @include text($align: '', $align-last: '', $indent: '', $spacing: '',
    $hyphens: '', $space: '', $break: '', $color: $white);
  @include border-radius($top-left: '', $top-right: '',
    $bottom-left: 100px, $bottom-right: 100px);

  &__container {
    @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: flex-start,
      $content: '', $row-gap: '', $column-gap: ''
    );

    @include max-width($value: map.get($breakpoints, lg1)) {
      @include flex($direction: column, $wrap: '', $justify: '', $align: center, 
        $content: '', $row-gap: 40px, $column-gap: ''
      );
    }
  }

  &__left{
    @include max-size($max-width: 683px, $max-height: '');

    @include max-width($value: map.get($breakpoints, lg1)) {
      @include flex($direction: column, $wrap: '', $justify: '', $align: center, 
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__hgroup {
    @include max-size($max-width: 650px, $max-height: '');

    @include max-width($value: map.get($breakpoints, lg1)) {
      @include text($align: center, $align-last: '', $indent: '', $spacing: '', 
        $hyphens: '', $space: '', $break: '', $color: ''
      );
      @include flex($direction: column, $wrap: '', $justify: '', $align: center, 
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__year {
    @include margin($top: 19px, $bottom: '', $left: '', $right: '');
  }

  &__advantages {
    @include margin($top: 38px, $bottom: '', $left: '', $right: '');
  }

  &__link {
    @include margin($top: 62px, $bottom: '', $left: '', $right: '');
    @include padding($top: 17.5px, $bottom: 17.5px, $left: 49.5px, $right: 49.5px);
    @include border-radius($all: 15px);
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(18),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
  }

  &__right {
    @include size($width: '', $height: 10px);
    @include flex-item($grow: '', $shrink: 1, $basis: '', 
      $order: '', $align: ''
    );

    @include max-width($value: map.get($breakpoints, lg1)) {
      @include size($width: '', $height: stretch);
    }
  }

  &__image {
    @include size($width: 100%, $height: '');
    @include border-radius($top-left: 20px, $top-right: 160px,
      $bottom-left: 160px, $bottom-right: 20px);
  }
}

.year {
  @include padding($top: 10px, $bottom: 10px, $left: 24px, $right: 24px);
  @include size($width: max-content, $height: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);
  @include border-radius($top-left: 30px, $top-right: 5px,
    $bottom-left: 5px, $bottom-right: 30px);
  @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(22),
    $height: '', $style: '', $stretch: '', $variant: ''
  );
  @include text($align: '', $align-last: '', $indent: '', $spacing: '',
    $hyphens: '', $space: '', $break: '', $color: $black);
  @include display($display: block, $visibility: '', $float: '', $clear: '',
    $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
    $opacity: ''
  );
}

.advantages {
  &__list {
    @include grid($columns: 1fr 1fr, $rows: '',
      $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
      $row-gap: 18px, $column-gap: 70px, $align-items: '', $justify-items: '',
      $align-content: '', $justify-content: ''
    );

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include grid($columns: 1fr, $rows: '', 
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '', 
        $row-gap: '', $column-gap: 10px, $align-items: '', $justify-items: '', 
        $align-content: '', $justify-content: ''
      );
    }
  }

  &__item {
    @include before($content: '', $display: block) {
      @include size($width: 24px, $height: 24px);
      @include background($image: url('./../assets/images/new_releases.svg'), $position: center,
        $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: ''
      );
      @include flex-item($grow: 0, $shrink: 0, $basis: '',
        $order: '', $align: ''
      );
    }

    & {
      // @include size($width: 317px, $height: '');
      // @include max-size($max-width: 317px, $max-height: '');
      @include font($family: 'Montserrat', $weight: $medium, $size: rem(16),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
        $content: '', $row-gap: '', $column-gap: 20px);
    }
  }
}