.workResults {
  @include margin($top: -100px, $bottom: '', $left: '', $right: '');
  @include padding($top: 130px, $bottom: 130px + 100px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $black-2);
  @include border-radius($top-left: 100px, $top-right: 100px,
    $bottom-left: '', $bottom-right: ''
  );

  @include max-width($value: map.get($breakpoints, sm1)) {
    @include padding($top: '', $bottom: 130px + 100px + 50px, $left: '', $right: '');
  }

  @include max-width($value: map.get($breakpoints, xs5)) {
    @include padding($top: '', $bottom: 130px + 100px + 100px, $left: '', $right: '');
  }

  &__middle {
    @include margin($top: 56px, $bottom: '', $left: '', $right: '');
  }

  &__bottom {
    @include margin($top: 130px, $bottom: '', $left: '', $right: '');
    @include size($width: '', $height: 339px);
    @include border-radius($all: 20px);
    @include flex($direction: column, $wrap: '', $justify: center, $align: '',
      $content: '', $row-gap: '', $column-gap: ''
    );

    @include min-width($value: map.get($breakpoints, xl1)) {
      @include background($image: url('./../assets/images/background.svg'), $position: '',
        $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: transparent);
      @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: flex-end,
        $content: '', $row-gap: '', $column-gap: ''
      );
    }

    @include max-width($value: map.get($breakpoints, xs5)) {
      @include margin($top: 200px, $bottom: '', $left: '', $right: '');
    }
  }

  &__mainBlock {
    @include padding($all: 50px);
    @include flex-item($grow: 1, $shrink: '', $basis: '',
      $order: '', $align: center);

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include size($width: 100%, $height: '');
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $white );
      @include border-radius($all: 20px);
    }
  }

  &__mainBlock-title {
    @include size($width: 700px, $height: '');
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include size($width: 100%, $height: '');
    }
  }

  &__mainBlock-subTitle {
    @include margin($top: 20px, $bottom: '', $left: '', $right: '');
    @include size($width: 700px, $height: '');
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include size($width: 100%, $height: '');
    }
  }

  &__mainBlock-paragraph {
    @include margin($top: rem(16), $bottom: '', $left: '', $right: '');
  }

  &__secondBlock {
    @include margin($top: 20px, $bottom: '', $left: '', $right: '');

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include flex-item($grow: 0, $shrink: '', $basis: '',
        $order: '', $align: center);
    }
  }

  &__secondBlock-submit {
    & {
      @include padding($top: 28.5px, $bottom: 28.5px, $left: 56.5px, $right: 56.5px);
      @include size($width: 348px, $height: 94px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange);
      @include border-radius($all: 15px);
      @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(18),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $white);
      @include flex($direction: '', $wrap: '', $justify: center, $align: flex-end,
        $content: '', $row-gap: '', $column-gap: ''
      );
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange-hover-3);
    }
  }
}

.results {
  &__list {
    @include grid($columns: 1fr 1fr 1fr, $rows: '',
      $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
      $row-gap: 10px, $column-gap: 10px, $align-items: '', $justify-items: center,
      $align-content: '', $justify-content: ''
    );

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include grid($columns: 1fr 1fr, $rows: '',
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
        $row-gap: '', $column-gap: '', $align-items: '', $justify-items: '',
        $align-content: '', $justify-content: ''
      );
    }

    @include max-width($value: map.get($breakpoints, md2)) {
      @include grid($columns: 1fr, $rows: '',
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
        $row-gap: '', $column-gap: '', $align-items: '', $justify-items: '',
        $align-content: '', $justify-content: ''
      );
    }
  }

  &__item {
    @include padding($all: 30px);
    @include size($width: 402px, $height: 215px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $light-grey-2);
    @include border-radius($all: 20px);
    @include flex($direction: column, $wrap: nowrap, $justify: space-between, $align: '',
      $content: '', $row-gap: 10px, $column-gap: ''
    );
  }

  &__title {
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(50),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__subTitle {
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(20),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }
}

.resultsLastItem {
  @include padding($all: 0px);
  @include flex($direction: row, $wrap: '', $justify: '', $align: '',
    $content: '', $row-gap: '', $column-gap: ''
  );

  &__link {
    & {
      @include padding($all: 30px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange);
      @include border-radius($all: 20px);
      @include flex-item($grow: 1, $shrink: '', $basis: '',
        $order: '', $align: ''
      );
      @include flex($direction: row, $wrap: wrap, $justify: space-between, $align: flex-end,
        $content: '', $row-gap: '', $column-gap: 10px);
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange-hover-3);
    }
  }

  &__subTitle {
    @include size($width: 160px, $height: '');
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(20),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
  }

  &__image {
    @include size($width: 65px, $height: 65px);
    @include background($image: url('./../assets/images/arrow\ up2.svg'), $position: center,
      $size: 65px, $repeat: no-repeat, $origin: '', $clip: '',
      $attachment: '', $color: ''
    );
    @include flex($direction: '', $wrap: '', $justify: center, $align: center,
      $content: '', $row-gap: '', $column-gap: ''
    );
  }
}