.accordion {
  @include size($width: 100%, $height: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $light-grey);
  @include border-radius($all: 20px);

  &.active {
    .accordion {
      &__button {
        @include after($content: '', $display: block) {
          @include settings($accent-color: '', $caret: '', $appearance: '',
            $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
            $transform: rotate(180deg), $aspect-ratio: '', $color-scheme: ''
          );
        }
      }

      &__subTitle {
        @include padding($top: 10px, $bottom: 40px, $left: '', $right: '');
        @include size($width: '', $height: auto);
      }
    }
  }

  &__button {
    & {
      @include padding($all: 30px);
      @include size($width: 100%, $height: 100%);
      @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(20),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $full-black);
      @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: center,
        $content: '', $row-gap: '', $column-gap: 20px);
    }

    @include after($content: '', $display: block) {
      @include size($width: 24px, $height: 24px);
      @include background($image: url('./../assets/images/arrow\ collapse.svg'), $position: center,
        $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: ''
      );
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: '');
      @include settings($accent-color: '', $caret: '', $appearance: '',
        $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
        $transform: rotate(0deg), $aspect-ratio: '', $color-scheme: ''
      );
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }
  }

  &__subTitle {
    @include padding($top: '', $bottom: '', $left: 40px, $right: 40px);
    @include size($width: '', $height: 0);
    @include overflow($all: hidden);
    @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
  }

  &__paragraph {
    & {
      @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $full-black);
    }

    @include nth-child($value: string.unquote('n+2')) {
      @include margin($top: rem(16), $bottom: '', $left: '', $right: '');
    }
  }
}