.steppedForm {
  @include margin($top: 130px, $bottom: 130px, $left: '', $right: '');

  &__form {
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $white);
    @include border-radius($all: 20px);
    @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: flex-start,
      $content: '', $row-gap: '', $column-gap: ''
    );

    @include max-width($value: map.get($breakpoints, lg1)) {
      @include flex($direction: column, $wrap: '', $justify: '', $align: '', 
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  $leftWidth: calc(569px + (40px * 2));

  &__left {
    @include padding($all: 40px);
    @include size($width: 100%, $height: '');
    @include max-size($max-width: $leftWidth, $max-height: '');
    @include flex-item($grow: '', $shrink: '', $basis: '', 
      $order: '', $align: center
    );

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include max-size($max-width: calc($leftWidth - 200px), $max-height: '');
    }

    @include max-width($value: map.get($breakpoints, lg1)) {
      @include max-size($max-width: $leftWidth, $max-height: '');
    }
  }

  &__right {
    @include flex-item($grow: '', $shrink: '', $basis: '',
      $order: '', $align: flex-end);
  }

  &__image {
    @include size($width: 100%, $height: '');

    @include min-width($value: map.get($breakpoints, xl1)) {
      @include margin($top: -55px, $bottom: '', $left: '', $right: '');
    }
  }

  &__title {
    @include font($family: 'NunitoSans_10pt', $weight: $regular, $size: rem(29),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include text($align: center, $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: ''
      );
    }
  }

  &__pagination {
    @include margin($top: 35px, $bottom: '', $left: '', $right: '');
    @include flex($direction: row, $wrap: wrap, $justify: center, $align: center,
      $content: '', $row-gap: 20px, $column-gap: 10px);
  }

  &__step {
    @include size($width: 86px, $height: 6px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $light-grey-5);
    @include border-radius($all: 10px);
    @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');

    &.passed {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange);
    }

    &.active {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange);
    }
  }

  &__slider {
    @include margin($top: 40px, $bottom: '', $left: '', $right: '');
  }

  &__controlButtons {
    @include margin($top: 117px, $bottom: '', $left: '', $right: '');
  }
}

.educationLevel {
  &__label {
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
    @include display($display: block, $visibility: '', $float: '', $clear: '',
      $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
      $opacity: ''
    );
  }

  &__select {
    @include margin($top: 10px, $bottom: '', $left: '', $right: '');
    @include padding($top: 19px, $bottom: 22px, $left: 30px, $right: 30px);
    @include size($width: 100%, $height: '');
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: transparent);
    @include border-radius($all: 20px);
    @include border($style: solid, $width: 2px, $color: $light-grey-4);
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
    @include settings($accent-color: '', $caret: '', $appearance: none,
      $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
      $transform: '', $aspect-ratio: '', $color-scheme: ''
    );
  }
}

.controlButtons {
  &__list {
    @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 10px);
  }

  &__item {
    @include nth-child($value: string.unquote('1')) {
      @include flex-item($grow: 0, $shrink: 1, $basis: '',
        $order: '', $align: ''
      );
    }

    @include nth-child($value: string.unquote('2')) {
      @include flex-item($grow: 1, $shrink: 1, $basis: '',
        $order: '', $align: ''
      );
    }
  }

  &__backButton {
    & {
      @include padding($top: 16.5px, $bottom: 16.5px, $left: 48.5px, $right: 48.5px);
      @include size($width: 100%, $height: '');
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $light-grey);
      @include border-radius($all: 20px);
      @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(18),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $black);
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $light-grey-hover );
    }
  }

  &__nextButton {
    & {
      @include padding($top: 16.5px, $bottom: 16.5px, $left: 48.5px, $right: 48.5px);
      @include size($width: 100%, $height: '');
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange);
      @include border-radius($all: 20px);
      @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(18),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $white);
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange-hover );
    }
  }
}