.chooseUniversity {
  @include padding($top: 130px, $bottom: 130px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);

  &__header {
    @include margin($top: '', $bottom: '', $left: auto, $right: auto);
    @include min-size($min-width: 300px, $min-height: '');
    @include max-size($max-width: 674px, $max-height: '');
  }

  &__subTitle {
    @include margin($top: 24px, $bottom: '', $left: 38px, $right: 38px);
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: center, $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }

  &__filter {
    @include margin($top: 45px, $bottom: '', $left: '', $right: '');
  }

  &__middle {
    @include margin($top: 40px, $bottom: '', $left: '', $right: '');
  }

  &__list {
    @include flex($direction: row, $wrap: wrap, $justify: center, $align: flex-start,
      $content: '', $row-gap: 10px, $column-gap: 10px);
  }
}

.filter {
  &__list {
    @include flex($direction: row, $wrap: wrap, $justify: center, $align: center,
      $content: '', $row-gap: 20px, $column-gap: 20px);
  }

  &__button {
    @include padding($top: 10px, $bottom: 10px, $left: 18px, $right: 18px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $light-grey-2);
    @include border-radius($all: 12px);
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
    @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');

    &.active {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange);
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $white);
    }
  }
}

.chooseUniversityCard {
  @include padding($all: 15px);
  @include size($width: 402px, $height: 349px);
  @include background($image: url('./../assets/images/university.png'), $position: center,
    $size: auto, $repeat: no-repeat, $origin: '', $clip: '',
    $attachment: '', $color: ''
  );
  @include border-radius($all: 20px);
  @include flex($direction: column, $wrap: wrap, $justify: space-between, $align: '',
    $content: '', $row-gap: '', $column-gap: ''
  );

  &__bottom {
    @include padding($all: 20px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $white);
    @include border-radius($all: 15px);
  }

  &__title {
    @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(17),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }

  &__date {
    @include margin($top: 11px, $bottom: '', $left: '', $right: '');
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 27px);
  }

  &__documents {
    @include size($width: 88px, $height: '');
    @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }

  &__dateRight {
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 5px);
  }

  &__time {
    @include padding($all: 10px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $light-grey-2);
    @include border-radius($all: 13px);
    @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }
}

.languages {
  &__list {
    @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 10px);
  }

  &__button {
    @include padding($all: 10px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $full-black-hover);
    @include border-radius($all: 15px);
    @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
  }
}