.workStages {
  @include padding($top: '', $bottom: 130px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);

  &__middle {
    @include margin($top: 56px, $bottom: '', $left: '', $right: '');
  }

  &__list {
    @include grid($columns: 1fr 1fr, $rows: '',
      $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
      $row-gap: 10px, $column-gap: 10px, $align-items: '', $justify-items: '',
      $align-content: '', $justify-content: ''
    );

    @include max-width($value: map.get($breakpoints, md2)) {
      @include grid($columns: 1fr, $rows: '',
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
        $row-gap: '', $column-gap: '', $align-items: '', $justify-items: '',
        $align-content: '', $justify-content: ''
      );
    }
  }
}

.workStagesCard {
  @include padding($all: 20px);
  @include min-size($min-width: '', $min-height: 264px);
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $light-grey);
  @include border-radius($all: 20px);

  @include max-width($value: map.get($breakpoints, md2)) {
    @include min-size($min-width: '', $min-height: 200px);
  }

  &__header {
    @include flex($direction: row, $wrap: wrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 20px);
  }

  &__stage {
    @include padding($top: 10px, $bottom: 10px, $left: 16px, $right: 16px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $orange);
    @include border-radius($all: 15px);
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
  }

  &__subTitle {
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__middle {
    @include margin($top: 24px, $bottom: '', $left: '', $right: '');
  }

  &__title {
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(25),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__paragraph {
    @include margin($top: 17px, $bottom: '', $left: '', $right: '');
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $dark-grey);
  }
}

.workStagesLastCard {
  @include padding($all: 30px);
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $orange);
  @include border-radius($all: 20px);

  &__title {
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(25),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
  }

  &__subTitle {
    @include margin($top: 10px, $bottom: '', $left: '', $right: '');
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
  }

  &__bottom {
    @include margin($top: 27px, $bottom: '', $left: '', $right: '');
  }

  &__form {
    @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: flex-start,
      $content: '', $row-gap: 10px, $column-gap: 10px);

    @include max-width($value: map.get($breakpoints, sm)) {
      @include flex($direction: column, $wrap: '', $justify: '', $align: '',
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__searchField {
    @include flex-item($grow: 1, $shrink: 1, $basis: '',
      $order: '', $align: ''
    );
    @include flex($direction: column, $wrap: '', $justify: '', $align: '',
      $content: '', $row-gap: 10px, $column-gap: ''
    );

    @include max-width($value: map.get($breakpoints, sm)) {
      @include size($width: 100%, $height: '');
    }
  }

  &__label {
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
    @include display($display: block, $visibility: '', $float: '', $clear: '',
      $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '',
      $opacity: ''
    );
  }

  &__tel,
  &__name {
    & {
      @include padding($top: 20px, $bottom: 20px, $left: 30px, $right: 37px);
      @include size($width: 100%, $height: '');
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: white);
      @include border($style: none, $width: '', $color: '');
      @include border-radius($all: 20px);
      @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(14),
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: $black);
      @include flex-item($grow: 1, $shrink: '', $basis: '',
        $order: '', $align: ''
      );
      @include settings($accent-color: '', $caret: $full-black, $appearance: '',
        $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
        $transform: '', $aspect-ratio: '', $color-scheme: ''
      );
    }

    @include placeholder() {
      @include font($family: inherit, $weight: inherit, $size: inherit,
        $height: '', $style: '', $stretch: '', $variant: ''
      );
      @include text($align: '', $align-last: '', $indent: '', $spacing: '',
        $hyphens: '', $space: '', $break: '', $color: inherit);
    }
  }

  &__submit {
    & {
      @include size($width: 51px, $height: 58px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $black);
      @include border-radius($all: 20px);
      @include flex-item($grow: '', $shrink: 0, $basis: '',
        $order: '', $align: flex-end);
      @include flex($direction: '', $wrap: '', $justify: center, $align: center,
        $content: '', $row-gap: '', $column-gap: ''
      );
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include max-width($value: map.get($breakpoints, sm)) {
      @include margin($top: 20px, $bottom: '', $left: '', $right: '');
      @include size($width: 100%, $height: '');
    }

    @include hover() {
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $full-black-hover-2);
    }
  }

  &__submitImage {
    @include size($width: 19.31px, $height: '');
    @include settings($accent-color: '', $caret: '', $appearance: '',
      $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
      $transform: rotate(-90deg), $aspect-ratio: '', $color-scheme: ''
    );
  }
}