.faq {
  @include padding($top: 130px, $bottom: 130px, $left: '', $right: '');
  @include max-width($value: map.get($breakpoints, lg1)) {
    @include padding($top: 50px, $bottom: 50px, $left: '', $right: '');
  }

  &__list {
    @include grid($columns: '', $rows: '', 
      $auto-columns: '', $auto-rows: '', $flow: '', $areas: '', 
      $row-gap: 10px, $column-gap: 10px, $align-items: '', $justify-items: '', 
      $align-content: '', $justify-content: ''
    );

    @include min-width($value: map.get($breakpoints, lg1)) {
      @include grid($columns: 505px 1fr, $rows: '',
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
        $row-gap: '', $column-gap: '', $align-items: '', $justify-items: '',
        $align-content: '', $justify-content: ''
      );
    }
  }

  &__item {
    & {
      @include padding($all: 20px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $light-grey);
      @include border-radius($all: 20px);
      @include flex($direction: column, $wrap: wrap, $justify: space-between, $align: stretch,
        $content: '', $row-gap: 17px, $column-gap: ''
      );
    }

    @include min-width($value: map.get($breakpoints, lg1)) {
      @include nth-child($value: string.unquote('1')) {
        @include grid-item($row: string.unquote($string: '1/2'), $column: string.unquote($string: '1/2'),
          $area: '', $justify: '', $align: '', $z-index: '', $order: ''
        );
      }

      @include nth-child($value: string.unquote('2')) {
        @include grid-item($row: string.unquote($string: '2/3'), $column: string.unquote($string: '1/2'),
          $area: '', $justify: '', $align: '', $z-index: '', $order: ''
        );
      }

      @include nth-child($value: string.unquote('3')) {
        @include grid-item($row: string.unquote($string: '1/3'), $column: string.unquote($string: '2/3'),
          $area: '', $justify: '', $align: '', $z-index: '', $order: ''
        );
      }

      @include nth-child($value: string.unquote('4')) {
        @include grid-item($row: string.unquote($string: '3/4'), $column: string.unquote($string: '1/3'),
          $area: '', $justify: '', $align: '', $z-index: '', $order: ''
        );
      }

      @include nth-child($value: string.unquote('5')) {
        @include grid-item($row: string.unquote($string: '4/5'), $column: string.unquote($string: '1/2'),
          $area: '', $justify: '', $align: '', $z-index: '', $order: ''
        );
      }

      @include nth-child($value: string.unquote('6')) {
        @include grid-item($row: string.unquote($string: '4/5'), $column: string.unquote($string: '2/3'),
          $area: '', $justify: '', $align: '', $z-index: '', $order: ''
        );
      }
    }
  }

  &__header {
    @include flex($direction: row, $wrap: nowrap, $justify: space-between, $align: center,
      $content: '', $row-gap: '', $column-gap: ''
    );
  }

  &__title {
    @include font($family: 'Montserrat', $weight: $semiBold, $size: rem(36),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }

  &__description {
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }

  &__paragraph {
    @include nth-child($value: string.unquote('n+2')) {
      @include margin($top: rem(16), $bottom: '', $left: '', $right: '');
    }
  }
}

.stage {
  @include size($width: 51px, $height: 51px);
  @include border($style: solid, $width: 2px, $color: $light-grey-3);
  @include border-radius($all: 15px);
  @include font($family: 'Montserrat', $weight: $medium, $size: rem(31),
    $height: '', $style: '', $stretch: '', $variant: ''
  );
  @include text($align: '', $align-last: '', $indent: '', $spacing: '',
    $hyphens: '', $space: '', $break: '', $color: $dark-grey-3);
  @include flex($direction: '', $wrap: '', $justify: center, $align: center,
    $content: '', $row-gap: '', $column-gap: ''
  );

  &.primary {
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $orange);
    @include border($style: none, $width: '', $color: '');
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $white);
  }
}

.labels {
  &__list {
    @include grid($columns: 606px 1fr, $rows: '',
      $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
      $row-gap: 20px, $column-gap: 60px, $align-items: '', $justify-items: '',
      $align-content: '', $justify-content: ''
    );

    @include max-width($value: map.get($breakpoints, xl1)) {
      @include grid($columns: 1fr 1fr, $rows: '',
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
        $row-gap: '', $column-gap: '', $align-items: '', $justify-items: '',
        $align-content: '', $justify-content: ''
      );
    }

    @include max-width($value: map.get($breakpoints, lg2)) {
      @include grid($columns: 1fr, $rows: '',
        $auto-columns: '', $auto-rows: '', $flow: '', $areas: '',
        $row-gap: '', $column-gap: '', $align-items: '', $justify-items: '',
        $align-content: '', $justify-content: ''
      );
    }
  }

  &__item {
    @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
      $content: '', $row-gap: '', $column-gap: 19px);

    @include max-width($value: map.get($breakpoints, sm1)) {
      @include display($display: block, $visibility: '', $float: '', $clear: '', 
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '', 
        $opacity: ''
      );
    }
  }

  &__label {
    @include padding($top: 7px, $bottom: 7px, $left: 10px, $right: 10px);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: $light-grey-4);
    @include border-radius($all: 40px);
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
    @include flex-item($grow: 0, $shrink: 0, $basis: '',
      $order: '', $align: ''
    );

    @include max-width($value: map.get($breakpoints, sm1)) {
      @include display($display: '', $visibility: '', $float: left, $clear: '', 
        $writing-mode: '', $block-size: '', $box-sizing: '', $vertical: '', 
        $opacity: ''
      );

      @include nth-child($value: string.unquote('1')) {
        @include flex-item($grow: '', $shrink: '', $basis: '', 
          $order: '', $align: center
        );
      }
    }
  }

  &__paragraph {
    @include font($family: 'Montserrat', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }
}