.dropdown {
  & {
    @include position($position: relative, $top: '', $bottom: '', $left: '', $right: '',
      $z-index: ''
    );
  }

  &.active {
    .dropdown {
      &__button {
        @include after($content: '', $display: block) {
          @include settings($accent-color: '', $caret: '', $appearance: '',
            $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
            $transform: rotate(180deg), $aspect-ratio: '', $color-scheme: ''
          );
        }
      }

      &__list {
        @include size($width: '', $height: auto);

        @include max-width($value: map.get($breakpoints, lg1)) {
          @include max-size($max-width: '', $max-height: 100px);
          @include overflow($overflow-x: hidden, $overflow-y: scroll);
        }
      }
    }
  }

  &__button {
    & {
      @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: center,
        $content: '', $row-gap: '', $column-gap: 5px);
    }

    @include after($content: '', $display: block) {
      @include size($width: 20px, $height: 20px);
      @include background($image: url('./../assets/images/dropdown-arrow-white.svg'), $position: center,
        $size: 10px, $repeat: no-repeat, $origin: '', $clip: '',
        $attachment: '', $color: ''
      );
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }
  }

  &__list {
    @include margin($top: 6px, $bottom: '', $left: '', $right: '');
    @include size($width: '', $height: 0);
    @include position($position: absolute, $top: '', $bottom: '', $left: -4px, $right: '',
      $z-index: 2);
    @include background($image: '', $position: '',
      $size: '', $repeat: '', $origin: '', $clip: '',
      $attachment: '', $color: silver);
    @include border-radius($all: 2px);
    @include overflow($all: hidden);
    @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
  }

  &__item {
    @include first-child() {
      @include margin($top: 4px, $bottom: '', $left: '', $right: '');
    }

    & {
      @include margin($top: '', $bottom: '', $left: 4px, $right: 4px);
    }

    @include nth-child($value: string.unquote('n+2')) {
      @include margin($top: 2px, $bottom: '', $left: '', $right: '');
    }

    @include last-child() {
      @include margin($top: '', $bottom: 4px, $left: '', $right: '');
    }
  }
}