:root {
  --black: #1C1B1F;
  --black-2: #2A292C;
  --full-black: black;
  --full-black-hover: rgba(0, 0, 0, 0.2);
  --full-black-hover-2: rgba(0, 0, 0, 0.4);
  --full-black-hover-3: #333333;
  --white: white;
  --white-hover: rgba(255, 255, 255, 0.15);
  --white-2: #EDEDED;
  --white-2-hover: rgba(237, 237, 237, 0.4);
  --white-3: rgba(237, 237, 237, 0.2);
  --orange: #F79923;
  --orange-hover: #d98923;
  --orange-hover-2: rgba(247, 153, 35, 0.23);
  --orange-hover-3: #de8b23;
  --background-black: #2A292C;
  --light-grey: #F4F4F4;
  --light-grey-hover: #cdcdcd;
  --light-grey-2: #F0F0F0;
  --light-grey-3: #BBBBBB;
  --light-grey-4: #E8E8E8;
  --light-grey-5: #CDCDCD;
  --light-grey-6: #D9D9D9;
  --dark-grey: #4F4C4C;
  --dark-grey-2: #3D3C3C;
  --dark-grey-3: #7F7F7F;
  --focus: aqua;
  --green: #46A04F;
  --popup-background: rgba(0, 0, 0, 0.4);
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
*:focus {
  outline: solid 1px rgba(0, 255, 255, 0.6);
  outline-offset: 0;
}

*[hidden] {
  display: none !important;
}

html,
head,
body,
ul,
menu,
ol,
span,
p,
dl,
dt,
dd,
a,
button,
input,
ul,
menu,
ol {
  margin: 0;
  padding: 0;
}

ul,
menu,
ol {
  list-style-type: none;
}

a {
  width: max-content;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  text-decoration-line: none;
  display: block;
}

button {
  width: max-content;
  cursor: pointer;
  background-color: transparent;
  border: none 0 transparent;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  text-align: left;
  color: inherit;
  display: block;
}
button:focus {
  outline-style: none;
}

.dropdown {
  position: relative;
}
.dropdown.active .dropdown__button::after {
  content: "";
  display: block;
  transform: rotate(180deg);
}
.dropdown.active .dropdown__list {
  height: auto;
}
@media (max-width: 1061px) {
  .dropdown.active .dropdown__list {
    max-height: 100px;
    overflow: hidden scroll;
  }
}
.dropdown__button {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  column-gap: 5px;
}
.dropdown__button::after {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background-image: url("./../assets/images/dropdown-arrow-white.svg");
  background-position: center;
  background-size: 10px;
  background-repeat: no-repeat;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.dropdown__list {
  margin-top: 6px;
  height: 0;
  position: absolute;
  left: -4px;
  z-index: 2;
  background-color: silver;
  border-radius: 2px;
  overflow: hidden;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.dropdown__item:first-child {
  margin-top: 4px;
}
.dropdown__item {
  margin-left: 4px;
  margin-right: 4px;
}
.dropdown__item:nth-child(n+2) {
  margin-top: 2px;
}
.dropdown__item:last-child {
  margin-bottom: 4px;
}

.mobile {
  min-width: 100vw;
  min-height: 100vh;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.mobile .header__search {
  display: block;
}
.mobile .header__phone {
  display: block;
}
.mobile .header__socialMedia {
  display: block;
}
.mobile .header__authorization {
  display: block;
}
.mobile .header__bottom {
  display: block;
}
.mobile .header__nav {
  display: block;
}
.mobile.active .mobile__block {
  transform: TranslateX(0);
}
.mobile.active .mobile__background {
  background-color: var(--full-black-hover-2);
}
.mobile__background {
  background-color: transparent;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.mobile__block {
  padding: 8px;
  width: 315px;
  height: 100%;
  background-color: var(--black);
  display: grid;
  grid-template-columns: 190px 1fr;
  row-gap: 15px;
  column-gap: 10px;
  align-items: center;
  justify-items: center;
  transform: TranslateX(-315px);
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
@media (max-width: 318px) {
  .mobile__block {
    width: 100%;
  }
}
.mobile .header__search {
  grid-row: 1/2;
  grid-column: 1/3;
}
.mobile .header__phone {
  grid-row: 2/3;
  grid-column: 1/2;
}
.mobile .header__socialMedia {
  grid-row: 2/3;
  grid-column: 2/3;
}
.mobile .header__authorization {
  width: 100%;
  grid-row: 3/4;
  grid-column: 1/3;
}
.mobile .header__nav {
  padding: 0;
  grid-row: 4/5;
  grid-column: 1/3;
  justify-self: left;
}
.mobile .search {
  width: 100%;
}
.mobile .search__form {
  max-width: 100%;
}
@media (max-width: 384px) {
  .mobile .search__form {
    max-width: 80%;
  }
}
.mobile .authorization__button {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mobile .nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.menuBurger {
  position: relative;
}
.menuBurger.active .menuBurger__item {
  background-color: transparent;
}
.menuBurger.active .menuBurger__item:nth-child(2)::before {
  content: "";
  display: block;
  transform: rotate(45deg);
}
.menuBurger.active .menuBurger__item:nth-child(2) {
  background-color: transparent;
}
.menuBurger.active .menuBurger__item:nth-child(2)::after {
  content: "";
  display: block;
  transform: rotate(-45deg);
}
.menuBurger__item {
  width: 30px;
  height: 4px;
  background-color: white;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.menuBurger__item:nth-child(2)::before {
  content: "";
  display: block;
  width: 30px;
  height: 4px;
  background-color: white;
  position: absolute;
  top: 0;
  left: 0;
  transition-duration: inherit;
  transition-timing-function: inherit;
}
.menuBurger__item:nth-child(2) {
  position: relative;
}
.menuBurger__item:nth-child(2)::after {
  content: "";
  display: block;
  width: 30px;
  height: 4px;
  background-color: white;
  position: absolute;
  top: 0;
  left: 0;
  transition-duration: inherit;
  transition-timing-function: inherit;
}
.menuBurger__item:nth-child(n+2) {
  margin-top: 6px;
}

.animate {
  transition-duration: 1s;
  transition-timing-function: ease;
}

.appereance_left_200 {
  transform: TranslateX(-200px);
  opacity: 0;
}

.appereance_right_200 {
  transform: TranslateX(200px);
  opacity: 0;
}

.opacity_0 {
  opacity: 0;
}

.appereance_top_100 {
  transform: TranslateY(-100px);
  opacity: 0;
}

.appereance_top_200 {
  transform: TranslateY(-200px);
  opacity: 0;
}

.button__orange {
  background-color: var(--orange);
  color: var(--white);
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
@media (hover: hover) {
  .button__orange:hover {
    background-color: var(--orange-hover);
  }
}
.button__grey {
  background-color: var(--light-grey);
  color: var(--black);
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
@media (hover: hover) {
  .button__grey:hover {
    background-color: var(--light-grey-hover);
  }
}

.sliderButtons__list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  column-gap: 49px;
}
.sliderButtons__item {
  font-size: 0rem;
}
.sliderButtons__prevButton, .sliderButtons__nextButton {
  width: 60px;
  height: 60px;
  background-color: #F79923;
  border-radius: 2000px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
@media (hover: hover) {
  .sliderButtons__prevButton:hover, .sliderButtons__nextButton:hover {
    background-color: #c67e23;
  }
}
.sliderButtons__prevButton {
  transform: rotate(90deg);
}
.sliderButtons__nextButton {
  transform: rotate(-90deg);
}
.sliderButtons__prevButtonImage {
  width: 19.31px;
}
.sliderButtons__nextButtonImage {
  width: 19.31px;
}

.popup {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
}
.popup.active .popup__background {
  background-color: var(--popup-background);
}
.popup__background {
  min-height: 100vh;
  width: 100%;
  height: 100%;
  background-color: transparent;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.popup__dialog[open] {
  padding: 20px;
  min-width: 200px;
  min-height: 100px;
  border-style: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-between;
  position: sticky;
}

.accordion {
  width: 100%;
  background-color: var(--light-grey);
  border-radius: 20px;
}
.accordion.active .accordion__button::after {
  content: "";
  display: block;
  transform: rotate(180deg);
}
.accordion.active .accordion__subTitle {
  padding-top: 10px;
  padding-bottom: 40px;
  height: auto;
}
.accordion__button {
  padding: 30px;
  width: 100%;
  height: 100%;
  font-family: "NunitoSans_10pt";
  font-weight: 600;
  font-size: 1.25rem;
  color: var(--full-black);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  column-gap: 20px;
}
.accordion__button::after {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background-image: url("./../assets/images/arrow collapse.svg");
  background-position: center;
  background-size: auto;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotate(0deg);
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.accordion__subTitle {
  padding-left: 40px;
  padding-right: 40px;
  height: 0;
  overflow: hidden;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.accordion__paragraph {
  font-family: "NunitoSans_10pt";
  font-weight: 500;
  font-size: 1rem;
  color: var(--full-black);
}
.accordion__paragraph:nth-child(n+2) {
  margin-top: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1 {
  font-family: "NunitoSans_10pt";
  font-weight: 700;
  font-size: 2.875rem;
  line-height: 53px;
}

h2 {
  font-family: "NunitoSans_10pt";
  font-weight: 700;
  font-size: 2.3125rem;
}

h3 {
  font-family: "Montserrat";
  font-weight: 600;
  font-size: 2.25rem;
}

.title {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
.title_black {
  color: var(--black);
}
.title_white {
  color: var(--white);
}

.bottom {
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  background-color: #E8E8E8;
}

.container {
  width: 1226px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 1267px) {
  .container {
    width: 90%;
  }
}

.lock_scroll {
  width: 100vw;
  height: 100vh !important;
  overflow: hidden;
}

.header {
  background-color: var(--black-2);
}
.header__top {
  padding-top: 12px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  column-gap: 10px;
}
.header__bottom {
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 20.5px;
}
@media (max-width: 1061px) {
  .header__search {
    display: none;
  }
  .header__phone {
    display: none;
  }
  .header__socialMedia {
    display: none;
  }
  .header__authorization {
    display: none;
  }
  .header__bottom {
    display: none;
  }
  .header__nav {
    display: none;
  }
}
@media (min-width: 1061px) {
  .header__menuBurger {
    display: none;
  }
}
@media (max-width: 1061px) {
  .header__menuBurger {
    display: block;
  }
}
@media (max-width: 384px) {
  .header__menuBurger {
    margin-top: -20px;
  }
}

.logo {
  width: max-content;
