.educationAdvantages {
  @include padding($top: 130px, $bottom: 130px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $black-2);
  @include border-radius($top-left: 100px, $top-right: 100px,
    $bottom-left: '', $bottom-right: ''
  );

  &__list {
    @include margin($top: 51px, $bottom: '', $left: '', $right: '');
    @include flex($direction: row, $wrap: nowrap, $justify: flex-start, $align: flex-start,
      $content: '', $row-gap: 10px, $column-gap: ''
    );
  }

  &__item {
    @include flex($direction: '', $wrap: '', $justify: center, $align: '',
      $content: '', $row-gap: '', $column-gap: ''
    );
  }

  &__sliderButtons {
    @include margin($top: 50px, $bottom: '', $left: '', $right: '');
  }
}

.educationAdvantagesCard {
  @include padding($all: 20px);
  @include size($width: 402px, $height: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);
  @include border-radius($all: 20px);
  @include flex($direction: column, $wrap: '', $justify: '', $align: '',
    $content: '', $row-gap: 61px, $column-gap: ''
  );

  @include max-width($value: map.get($breakpoints, sm1)) {
    @include size($width: 100%, $height: '');
  }

  &__header {
    @include flex($direction: column, $wrap: '', $justify: '', $align: '',
      $content: '', $row-gap: 10px, $column-gap: ''
    );
  }

  &__title {
    @include font($family: 'Montserrat', $weight: $bold, $size: rem(25),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }

  &__subTitle {
    @include font($family: 'Montserrat', $weight: $regular, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $black);
  }

  &__image {
    @include flex-item($grow: '', $shrink: '', $basis: '',
      $order: '', $align: center);
  }
}