@mixin font-face($name, $weight, $style, $path) {
  @font-face {
    font-family: $name;
    font-weight: $weight;
    font-style: $style;
    src: url($path) format("TrueType");
  }
}

@include font-face('Montserrat', $thin, normal, "./../assets/fonts/Montserrat/Montserrat-Thin.ttf");
@include font-face('Montserrat', $thin, italic, "./../assets/fonts/Montserrat/Montserrat-ThinItalic.ttf");

@include font-face('Montserrat', $light, normal, "./../assets/fonts/Montserrat/Montserrat-Light.ttf");
@include font-face('Montserrat', $light, italic, "./../assets/fonts/Montserrat/Montserrat-LightItalic.ttf");

@include font-face('Montserrat', $regular, normal, "./../assets/fonts/Montserrat/Montserrat-Regular.ttf");

@include font-face('Montserrat', $medium, normal, "./../assets/fonts/Montserrat/Montserrat-Medium.ttf");
@include font-face('Montserrat', $medium, italic, "./../assets/fonts/Montserrat/Montserrat-MediumItalic.ttf");

@include font-face('Montserrat', $bold, normal, "./../assets/fonts/Montserrat/Montserrat-Bold.ttf");
@include font-face('Montserrat', $bold, italic, "./../assets/fonts/Montserrat/Montserrat-BoldItalic.ttf");

@include font-face('Montserrat', $black, normal, "./../assets/fonts/Montserrat/Montserrat-Black.ttf");
@include font-face('Montserrat', $black, italic, "./../assets/fonts/Montserrat/Montserrat-BlackItalic.ttf");