.team {
  @include padding($top: 130px, $bottom: 200px, $left: '', $right: '');
  @include background($image: '', $position: '',
    $size: '', $repeat: '', $origin: '', $clip: '',
    $attachment: '', $color: $white);

  &__middle {
    @include margin($top: 56px, $bottom: '', $left: '', $right: '');
    @include flex($direction: row, $wrap: wrap, $justify: center, $align: center,
      $content: '', $row-gap: '', $column-gap: 60px);
  }

  &__left{
    @include size($width: 600px, $height: '');
  }

  &__list {
    @include flex($direction: row-reverse, $wrap: wrap, $justify: flex-start, $align: flex-end,
      $content: '', $row-gap: '', $column-gap: 10px);
  }

  &__sliderButtons {
    @include margin($top: 133px, $bottom: '', $left: '', $right: '');
  }
}

.teadCard {
  @include position($position: relative, $top: '', $bottom: '', $left: '', $right: '',
    $z-index: ''
  );

  &__image {
    @include border-radius($all: 20px);
  }

  &__name {
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(30),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__description {
    @include margin($top: 22px, $bottom: '', $left: '', $right: '');
    @include size($width: 378px, $height: '');
    @include font($family: 'NunitoSans_10pt', $weight: $medium, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $dark-grey-2);
  }

  &__shortBlock {
    @include before($content: '', $display: block) {
      @include size($width: 100%, $height: 4px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: $orange);
      @include border-radius($all: 22px);
    }

    & {
      @include margin($top: 22px, $bottom: '', $left: '', $right: '');
      @include position($position: absolute, $top: '', $bottom: '', $left: '', $right: '',
        $z-index: ''
      );
      @include flex($direction: column, $wrap: wrap, $justify: center, $align: flex-start,
        $content: '', $row-gap: '', $column-gap: ''
      );
    }
  }

  &__shortName {
    @include margin($top: 12px, $bottom: '', $left: '', $right: '');
    @include font($family: 'NunitoSans_10pt', $weight: $semiBold, $size: rem(16),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $full-black);
  }

  &__shortDescription {
    @include margin($top: 4px, $bottom: '', $left: '', $right: '');
    @include font($family: 'Montserrat', $weight: $regular, $size: rem(14),
      $height: '', $style: '', $stretch: '', $variant: ''
    );
    @include text($align: '', $align-last: '', $indent: '', $spacing: '',
      $hyphens: '', $space: '', $break: '', $color: $dark-grey);
  }
}