.menuBurger {
  @include position($position: relative, $top: '', $bottom: '', $left: '', $right: '',
    $z-index: '');

  &.active {
    .menuBurger {
      &__item {
        & {
          @include background($image: '', $position: '',
            $size: '', $repeat: '', $origin: '', $clip: '',
            $attachment: '', $color: transparent);
        }

        @include nth-child($value: string.unquote('2')) {
          @include before($content: '', $display: block) {
            @include settings($accent-color: '', $caret: '', $appearance: '',
              $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
              $transform: rotate(45deg), $aspect-ratio: '', $color-scheme: ''
            );
          }

          & {
            @include background($image: '', $position: '',
              $size: '', $repeat: '', $origin: '', $clip: '',
              $attachment: '', $color: transparent);
          }

          @include after($content: '', $display: block) {
            @include settings($accent-color: '', $caret: '', $appearance: '',
              $pointer-events: '', $resize: '', $user-select: '', $cursor: '',
              $transform: rotate(-45deg), $aspect-ratio: '', $color-scheme: ''
            );
          }
        }
      }
    }
  }

  &__item {
    & {
      @include size($width: 30px, $height: 4px);
      @include background($image: '', $position: '',
        $size: '', $repeat: '', $origin: '', $clip: '',
        $attachment: '', $color: white);
      @include transition($duration: 0.3s, $function: ease, $delay: '', $property: '');
    }

    @include nth-child($value: string.unquote('2')) {
      @include before($content: '', $display: block) {
        @include size($width: 30px, $height: 4px);
        @include background($image: '', $position: '',
          $size: '', $repeat: '', $origin: '', $clip: '',
          $attachment: '', $color: white);
        @include position($position: absolute, $top: 0, $bottom: '', $left: 0, $right: '',
          $z-index: ''
        );
        @include transition($duration: inherit, $function: inherit, $delay: '', $property: '');
      }

      & {
        @include position($position: relative, $top: '', $bottom: '', $left: '', $right: '',
          $z-index: ''
        );
      }

      @include after($content: '', $display: block) {
        @include size($width: 30px, $height: 4px);
        @include background($image: '', $position: '',
          $size: '', $repeat: '', $origin: '', $clip: '',
          $attachment: '', $color: white);
        @include position($position: absolute, $top: 0, $bottom: '', $left: 0, $right: '',
          $z-index: ''
        );
        @include transition($duration: inherit, $function: inherit, $delay: '', $property: '');
      }
    }

    @include nth-child($value: string.unquote('n+2')) {
      & {
        @include margin($top: 6px, $bottom: '', $left: '', $right: '');
      }
    }
  }
}